








import { Component, Emit, Prop, Vue } from 'vue-property-decorator'
import { Article } from '../../domain/articles'

@Component
export default class ArticleExcerpt extends Vue {
  @Prop()
  excerpt!: ReturnType<Article['getExcerpt']>

  @Emit()
  onClick() {
    return this.excerpt.id
  }
}
