






import { Component, Inject, Vue } from 'vue-property-decorator'
import { State } from '../state'
import { Theme } from './Theme'

@Component
export default class Theming extends Vue {
  @Inject()
  state!: State

  get theme(): string {
    switch (this.state.theme) {
      case Theme.DARK:
        return 'dark'
      case Theme.LIGHT:
      default:
        return 'light'
    }
  }
}
